
.header {
  position: relative;
  color: #fff;
  overflow: hidden;
  background: rgba(7,17,27,0.5);
}
.header .content-wrapper {
  position: relative;
  padding: 48px 24px 32px 48px;
  font-size: 0;
}
.header .content-wrapper .avatar {
  display: inline-block;
  vertical-align: top;
}
.header .content-wrapper .avatar .img {
  border-radius: 2px;
}
.header .content-wrapper .content {
  display: inline-block;
  margin-left: 32px;
}
.header .content-wrapper .content .title {
  margin: 2px 0 16px 0;
}
.header .content-wrapper .content .title .brand {
  display: inline-block;
  vertical-align: top;
  width: 60px;
  height: 36px;
  background-image: url("brand@2x.png");
  background-size: 60px 32px;
  background-repeat: no-repeat;
}
@media (-webkit-min-divice-pixel-ratio: 3), (min-divice-pixel-ration: 3) {
.header .content-wrapper .content .title .brand {
    background-image: url("brand@3x.png");
}
}
.header .content-wrapper .content .title .name {
  margin-left: 12px;
  font-size: 32px;
  line-height: 32px;
  font-weight: bold;
}
.header .content-wrapper .content .description {
  margin-bottom: 20px;
  line-height: 24px;
  font-size: 24px;
}
.header .content-wrapper .content .support .icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-right: 8px;
  background-size: 24px 24px;
  background-repeat: no-repeat;
}
.header .content-wrapper .content .support .icon.decrease {
  background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-divice-pixel-ratio: 3), (min-divice-pixel-ration: 3) {
.header .content-wrapper .content .support .icon.decrease {
    background-image: url("decrease_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.discount {
  background-image: url("discount_1@2x.png");
}
@media (-webkit-min-divice-pixel-ratio: 3), (min-divice-pixel-ration: 3) {
.header .content-wrapper .content .support .icon.discount {
    background-image: url("discount_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.guarantee {
  background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-divice-pixel-ratio: 3), (min-divice-pixel-ration: 3) {
.header .content-wrapper .content .support .icon.guarantee {
    background-image: url("guarantee_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.invoice {
  background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-divice-pixel-ratio: 3), (min-divice-pixel-ration: 3) {
.header .content-wrapper .content .support .icon.invoice {
    background-image: url("invoice_1@3x.png");
}
}
.header .content-wrapper .content .support .icon.special {
  background-image: url("special_1@2x.png");
}
@media (-webkit-min-divice-pixel-ratio: 3), (min-divice-pixel-ration: 3) {
.header .content-wrapper .content .support .icon.special {
    background-image: url("special_1@3x.png");
}
}
.header .content-wrapper .content .support .text {
  line-height: 24px;
  font-size: 20px;
}
.header .content-wrapper .support-count {
  position: absolute;
  right: 24px;
  bottom: 28px;
  padding: 0 16px;
  height: 48px;
  line-height: 48px;
  border-radius: 28px;
  background: rgba(0,0,0,0.2);
  text-align: center;
}
.header .content-wrapper .support-count .count {
  font-size: 20px;
  vertical-align: top;
}
.header .content-wrapper .support-count .icon-keyboard_arrow_right {
  margin-left: 4px;
  line-height: 48px;
  font-size: 20px;
}
.header .bulletin-wrapper {
  position: relative;
  height: 56px;
  line-height: 56px;
  padding: 0 44px 0 24px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(0,0,0,0.2);
}
.header .bulletin-wrapper .bulletin-title {
  display: inline-block;
  vertical-align: top;
  margin-top: 16px;
  width: 44px;
  height: 24px;
  background-image: url("bulletin@2x.png");
  background-size: 44px 24px;
  background-repeat: no-repeat;
}
@media (-webkit-min-divice-pixel-ratio: 3), (min-divice-pixel-ration: 3) {
.header .bulletin-wrapper .bulletin-title {
    background-image: url("bulletin@3x.png");
}
}
.header .bulletin-wrapper .bulletin-text {
  vertical-align: top;
  margin: 0 8px 0 8px;
  font-size: 28px;
  font-weight: 200;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
  position: absolute;
  font-size: 24px;
  right: 24px;
  top: 16px;
}
.header .background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(7,17,27,0.8);
}
.header .detail .detail-wrapper {
  min-height: 100%;
  width: 100%;
}
.header .detail .detail-wrapper .detail-main {
  margin-top: 128px;
  padding-bottom: 128px;
}
.header .detail .detail-wrapper .detail-main .name {
  line-height: 32px;
  text-align: center;
  font-size: 32px;
  font-weight: 700;
}
.header .detail .detail-wrapper .detail-main .star-wrapper {
  margin-top: 32px;
  padding: 4px 0;
  text-align: center;
}
.header .detail .detail-close {
  position: relative;
  width: 64px;
  height: 64px;
  margin: -128px auto 0 auto;
  clear: both;
  font-size: 64px;
}


